#main div {
  min-width: 42%;
  max-width: 100%;
  margin-top: 2rem;
}

#main div a:focus,
#main div a:hover {
  transform: scale(1.01);
  filter: contrast(120%) brightness(120%);
}

#mastodon {
  background-color: #2B90D9;
}

/* Initially generated using https://sharingbuttons.io since then improved. */
#main div a {
  text-decoration: none;
  color: #fff;
  margin: .8rem;
  display: block;
  border-radius: 0.3125rem;
  transition: 25ms ease-out;
  padding: .75rem 1rem;
}

#main div a svg {
  background-color: transparent;
  display: block;
  width: 1.1rem;
  height: 1.1rem;
  margin-bottom: .1rem;
  margin-right: 0.4rem;
  vertical-align: top;
}

#main div a svg:not(.fill) {
  stroke: #fff;
  fill: none;
}

.fill {
  fill: #fff;
  stroke: none;
}

.stroke-2x {
  stroke-width: 2px;
}

#twitter {
  background: #55acee;
}

#pinterest {
  background: #bd081c;
}

#facebook {
  background: #3b5998;
}

#reddit {
  background: #ff4400;
}

#email {
  background: #777;
}

#whatsapp {
  background: #25D366;
}

#telegram {
  background: #54A9EB;
}
